import React from 'react'
import PageProvider from '../components/pageProvider'
import Link from 'gatsby-link'

const IndexPage = () => (
  <div>
    <h1>Hi people</h1>
    <PageProvider />
    <Link to="/page-2/">Go to page 2</Link>
  </div>
)

export default IndexPage



